/* HelpAndSupportPage.css */
.HelpNSupport-main {
  display: flex;
  justify-content: center;
  align-items: center;
}

.help-support-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.left-side {
  flex: 1;
  text-align: center;
}

.left-side img {
  max-width: 100%;
}

.right-side {
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex: 1;
  padding: 20px;
}

.right-side h1 {
  font-size: 32px;
  margin-bottom: 10px;
}

.right-side p {
  font-size: 16px;
  margin-bottom: 5px;
}

.contactHelpNSupport {
  display: flex;
  align-items: center;
  flex-direction: row;
}

/* Responsive styles for device width less than 768px */
@media (max-width: 767px) {
  .help-support-container {
    flex-direction: column;
  }

  .left-side,
  .right-side {
    width: 100%;
  }
}
