h1,
h2 {
  text-transform: capitalize;
}

/* Dashboard Page Css */
.ADash-mainContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}
.ADash-main {
  display: flex;
  flex-direction: column;
  margin: 10px;
  width: 100vw;
}

.ADash-FirstSecHeader {
  display: flex;
  justify-content: center;
  text-align: center;
}
.ADash-FirstSecMain {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 20px;
  border-radius: 20px;
  background: var(--colorWhite);
  box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
    rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
}

.ADFSItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.ADTSLeft-main button{
  margin: 5px;
}
.ADash-SecondSecMain {
  margin: 20px 0px;
  display: flex;
}
.ADash-SSecItems {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
}
.ADash-SSecItem {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 200px;
  padding: 10px;
  margin: 10px;
  border-radius: 20px;
  box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
    rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
}

.ADash-SSecItem:nth-child(odd) {
  background-color: #265ae9b7;
  color: var(--colorWhite);
}
.ADash-SSecItem:nth-child(even) {
  background-color: var(--colorWhite);
  color: #265ae9b7;
}

.ADash-ThirdSecMain {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 20px;
  border-radius: 2px;
  background: var(--colorWhite);
  box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
    rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
}

.ADTSLeft-main {
  margin: 20px;
  width: 45%;
  padding: 20px;
  border-radius: 2px;
  background: var(--colorWhite);
  box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
    rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
}
.ADTSRight-main {
  text-align: center;
  width: 45%;
}

@media (max-width: 1000px) {
  .ADTSLeft-main {
    margin: 20px;
    width: 100%;
  }
  .ADTSRight-main {
    margin: 20px;
    width: 100%;
  }
}
/* Add New Page css */

.Profiles-mainContailer,
.IncompleteR-mainContailer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.507);
  border-radius: 20px 20px 20px 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
  text-align: left;
  padding: 20px;
  margin: 20px 0px 0px 0px;
}

table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
}

th {
  border-bottom: 2px solid #ddd;
  padding: 1rem 0rem;
  text-align: center;
}

.Profiles-mainContailer td,
.BDash-ThirdSecMain td,
.IncompleteR-mainContailer td,
.ADTSLeft-main td {
  border-bottom: 2px solid #ddd;
  padding: 1rem 0rem 1rem 1rem;
  text-align: center;
}



@media (max-width: 500px) {
  .Profiles-mainContailer table {
    width: 100%;
    display: flex;
    flex-direction: column;
    overflow-x: auto;
  }
}

.Profiles-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.Profiles-mainContailer .button-group {
  position: relative;
  display: inline-block;
}

.Profiles-mainContailer .dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 80px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  padding: 12px 16px;
  z-index: 1;
}

.Profiles-mainContailer .button-group.open .dropdown-content {
  display: block;
}

.AddNew-mainContainer {
  text-align: center;
}

.AddNewNav-link {
  text-decoration: none;
  margin: 10px;
  color: #000000;
}

.AddNewNav-link.active {
  border-right: none;
  text-decoration: underline;
  text-underline-offset: 10px;
  color: var(--colorMain);
  background: none;
}

.Salary-mainContainer,
.Rent-mainContainer,
.Bill-mainContainer,
.Holidays-mainContainer {
  text-align: center;
}

.SalaryNav,
.RentNav,
.BillNav,
.HolidaysNav {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.SalaryNav-link,
.RentNav-link,
.BillNav-link
.HolidaysNav-link {
  text-decoration: none;
  margin: 10px;
  color: #000000;
}

.SalaryNav-link.active,
.RentNav-link.active,
.BillNav-link.active,
.HolidaysNav-link.active {
  border-right: none;
  text-decoration: underline;
  text-underline-offset: 10px;
  color: var(--colorMain);
  background: none;
}

/* SalaryPaymentProcess.jsx */

.AttendanceTable-main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.AttendanceTableForm {
  width: 500px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-flow: row;
}
@media (max-width: 600px) {
  .AttendanceTableForm {
    width: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
  }
}

.AttendanceTableMain {
  overflow-x: auto;
}

.AttendanceTable {
  max-width: 90vw;
}

.ATM-FixedSection {
  position: sticky;
  left: 0;
  background: #f2f2f2;
  z-index: 1;
}

.AttendanceTableDBtn {
  display: flex;
  justify-content: flex-end;
  margin: 10px;
}

.AttendanceTable table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  border: 1px solid black;
  border-collapse: collapse;
  margin-bottom: 20px;
}

.AttendanceTable th,
.AttendanceTable td {
  text-align: left;
  padding: 8px;
  border: 1px solid black;
  border-collapse: collapse;
}

.AttendanceTable tr:nth-child(even) {
  background-color: #f2f2f2;
}

@media (max-width: 600px) {
  .AttendanceTable {
    max-width: 80vw;
  }

  .AttendanceTable th,
  .AttendanceTable td {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

