/* Dashboard Page Css */
.BDash-mainContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}
.BDash-main {
  display: flex;
  flex-direction: column;
  margin: 10px;
  width: 100vw;
}

.BDash-FirstSecHeader {
  display: flex;
  justify-content: center;
  text-align: center;
}
.BDash-FirstSecMain {
  display: flex;
  flex-direction: column;
  padding: 20px;
  border-radius: 20px;
  background: var(--colorWhite);
  box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
    rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
}

.BDash-FirstSec {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.BD-FSecLeft {
  width: 75%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.BD-FSecLeft .BD-FSLUser {
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.BD-FSecLeft .BD-FSLUser .BD-FSLUImg {
  padding-right: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.BD-FSecLeft .BD-FSLUser .BD-FSLUImg img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 2px solid var(--colorWhite);
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px,
    rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.BD-FSecLeft .BD-FSLUser .BD-FSLUDetail {
  display: flex;
  flex-direction: column;
  text-align: left;
}

.BD-FSecRight {
  width: 25%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.BD-FSecRight button {
  max-width: 150px;
}

.BDash-SecondSecMain {
  margin: 20px 0px;
  display: flex;
}
.BDash-SSecItems {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
}
.BDash-SSecItem {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 200px;
  padding: 10px;
  margin: 10px;
  border-radius: 20px;
  box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
    rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
}

.BDash-SSecItem:nth-child(odd) {
  background-color: #265ae9b7;
  color: var(--colorWhite);
}
.BDash-SSecItem:nth-child(even) {
  background-color: var(--colorWhite);
  color: #265ae9b7;
}

.BDash-ThirdSecMain {
  display: flex;
  flex-direction: column;
  padding: 20px;
  border-radius: 20px;
  background: var(--colorWhite);
  box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
    rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
}

.DateInputContainer{
  display: flex;
  justify-content: space-between;
}
.DateInput input{
  border-radius: 10px;
  padding: 2px;
  margin: 0px 10px;
}

/* Profiles page css */

.Profiles-mainContailer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.507);
  border-radius: 20px 20px 20px 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
  text-align: left;
  padding: 20px;
  margin: 20px 0pc 0px 0px;
}

table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
}

th {
  border-bottom: 2px solid #ddd;
  padding: 1rem 0rem;
  text-align: center;
}

.Profiles-mainContailer td, .BDash-ThirdSecMain td {
  border-bottom: 2px solid #ddd;
  padding: 1rem 0rem 1rem 1rem;
  text-align: center;
}

@media (max-width: 500px) {
  .Profiles-mainContailer table {
    width: 100%;
    display: flex;
    flex-direction: column;
    overflow-x: auto;
  }
}

.Profiles-header{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.Profiles-mainContailer .button-group {
  position: relative;
  display: inline-block;
}

.Profiles-mainContailer .dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 80px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  padding: 12px 16px;
  z-index: 1;
}

.Profiles-mainContailer .button-group.open .dropdown-content {
  display: block;
}
