@import url("~@fontsource/poppins");

:root {
  --colorMain: #265ae9;
  --colorMainLight: #4772e8;
  --colorWhite: #fff;
  --colorBlack: #222;
  --colorSidebar: rgb(238, 241, 247);
  --colorButton: #003bdc;
  --colorButtonLight: #305fe0;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
}

.Home-mainContainer {
  overflow-x: hidden;
}

.modal-done-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}