/* Staff Dashboard page Code */
.SDash-mainConatainer {
  display: flex;
  justify-content: center;
  align-items: center;
}
.SDash-main {
  display: flex;
  flex-direction: column;
  margin: 10px;
  width: 100vw;
}
.SDash-FirstSecMain {
  display: flex;
  flex-direction: column;
  padding: 20px;
  border-radius: 20px;
  background: var(--colorWhite);
  box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
    rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
}

.SDash-FirstSec {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.SD-FSecLeft {
  width: 75%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.SD-FSecLeft .SD-FSLUser {
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.SD-FSecLeft .SD-FSLUser .SD-FSLUImg {
  padding-right: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.SD-FSecLeft .SD-FSLUser .SD-FSLUImg img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 2px solid var(--colorWhite);
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px,
    rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.SD-FSecLeft .SD-FSLUser .SD-FSLUDetail {
  display: flex;
  flex-direction: column;
  text-align: left;
}
.SD-FSecRight {
  width: 25%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.SD-FSecRight button {
  max-width: 150px;
}

.SDash-SecondSecMain {
  margin: 20px 0px;
  display: flex;
}
.SDash-SSecItems {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
}
.SDash-SSecItem {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 200px;
  padding: 10px;
  margin: 10px;
  border-radius: 20px;
  box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
    rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
}

.SDash-SSecItem:nth-child(odd) {
  background-color: #265ae9b7;
  color: var(--colorWhite);
}
.SDash-SSecItem:nth-child(even) {
  background-color: var(--colorWhite);
  color: #265ae9b7;
}

@media (max-width: 500px) {
  .SDash-FirstSec {
    display: flex;
    flex-direction: column;
  }
  .SD-FSecLeft .SD-FSLUser {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .SD-FSecLeft .SD-FSLUser .SD-FSLUImg {
    padding-right: 0px;
    margin-bottom: 10px;
  }
  .SDash-FirstSecHeader {
    width: 100%;
    text-align: center;
  }
}

.Attend-mainContainer {
  margin: 20px 0px 20px 0px;
}
.Attend-mainContainer,
.SDash-ThirdSecMain,
.Holiday-SecContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.507);
  border-radius: 20px 20px 20px 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
  text-align: left;
  padding: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
}

th {
  border-bottom: 2px solid #ddd;
  padding: 1rem 0rem;
  text-align: center;
}

.Attend-mainContainer td,
.SDash-ThirdSecMain td,
.Holiday-SecContainer td {
  border-bottom: 2px solid #ddd;
  padding: 1rem 0rem 1rem 1rem;
  text-align: center;
}

@media (max-width: 500px) {
  .Holiday-SecContainer table {
    width: 100%;
    display: flex;
    flex-direction: column;
    overflow-x: auto;
  }
}

/* Profile page css */

/* Main container */
.Profile-mainContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Main section */
.Profile-main {
  max-width: 1200px;
  margin: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.Profile-ConatainerLeft {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.Profile-ConatainerRight {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

/* Profile picture container */
.profile-picture-container {
  position: relative;
  width: 250px;
  height: 250px;
  border-radius: 50%;
  margin: 20px;
  overflow: hidden;
  border: 5px solid var(--colorWhite);
  box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px,
    rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}

/* Profile picture */
.profile-picture {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Overlay for uploading image */
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.overlay:hover {
  opacity: 1;
}

/* Upload label */
.upload-label {
  color: var(--colorWhite);
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
}

/* File input */
.profilePictureInput {
  display: none;
}

/* Profile detail container */
.Profile-CLDetail {
  margin-left: 20px;
}

/* Heading */
.Profile-CLDetail h2 {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

/* Upper container */
.Profile-CRUpper ul {
  max-width: 500px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;
  padding: 0;
  margin: 0;
}

/* Upper container list item */
.Profile-CRUpper li {
  display: flex;
  flex-direction: column;
  margin: 10px 10px 10px 10px;
}

/* Upper container paragraph */
.Profile-CRUpper p {
  margin-right: 10px;
  font-weight: bold;
  width: 100px;
}

.Profile-CRUpper input[type="text"]:read-only {
  background-color: #f2f2f2;
  cursor: not-allowed;
  width: 200px;
  padding: 20px;
  margin: 10px 0;
  border-radius: 6px;
  font-size: 20px;
  border: 2px solid #ccc;
}

/* Lower container */
.Profile-CRLower {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.Profile-CRLower ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;
  padding: 0;
  margin: 0;
}

/* Lower container list item */
.Profile-CRLower li {
  display: flex;
  flex-direction: column;
  margin: 10px 10px 10px 10px;
}

.Profile-CRLower p {
  margin-right: 10px;
  font-weight: bold;
  width: 100px;
}

/* Input fields */
.Profile-CRLower input[type="text"] {
  padding: 20px;
  margin: 20px 0;
  border-radius: 6px;
  font-size: 20px;
  width: 300px;
  border: 2px solid #ccc;
}

/* Read-only input fields */
.Profile-CRLower input[type="text"]:read-only {
  background-color: #f2f2f2;
  cursor: not-allowed;
}

/* Edit button */
.Profile-CRLower .edit-button {
  background-color: #007bff;
  color: var(--colorWhite);
  border: none;
  padding: 8px 16px;
  border-radius: 5px;
  cursor: pointer;
}

.Profile-CRLower .edit-button:hover {
  background-color: #0069d9;
}

/* Confirm button */
.Profile-CRLower .confirm-button {
  background-color: #28a745;
  color: var(--colorWhite);
  border: none;
  padding: 8px 16px;
  border-radius: 5px;
  cursor: pointer;
}

.Profile-CRLower .confirm-button:hover {
  background-color: #218838;
}

/* Disabled button */
.Profile-CRLower .disabled-button {
  background-color: #6c757d;
  color: var(--colorWhite);
  border: none;
  padding: 8px 16px;
  border-radius: 5px;
  cursor: not-allowed;
}

.Profile-CRLowerButton {
  width: 100px;
  margin: 0px 20px;
  color: var(--colorWhite);
  border: none;
  padding: 8px 16px;
  border-radius: 5px;
  margin-right: 10px;
  cursor: pointer;
}

/* Cancel button */
.Profile-CRLower .cancel-button {
  background-color: #dc3545;
  color: var(--colorWhite);
  border: none;
  padding: 8px 16px;
  border-radius: 5px;
  margin-right: 10px;
  cursor: pointer;
}

.Profile-ConatainerLeft ul {
  display: flex;
  flex-direction: column;
  justify-content: center;
  list-style: none;
  padding: 0;
  margin: 0;
}

/* Lower container list item */
.Profile-ConatainerLeft li {
  display: flex;
  flex-direction: column;
  margin: 10px 10px 10px 10px;
}

/* Input fields */
.Profile-ConatainerLeft input[type="text"],.Profile-ConatainerLeft input[type="password"] {
  padding: 5px;
  border-radius: 6px;
  font-size: 20px;
  width: 200px;
  border: 2px solid #ccc;
}

.Profile-ConatainerLeft input[type="text"]:read-only, .Profile-ConatainerLeft input[type="password"]{
  background-color: #f2f2f2;
  cursor: not-allowed;
}

.Profile-ConatainerLeft p {
  margin-right: 10px;
  font-weight: bold;
  width: 100px;
}

.Profile-CRLower .cancel-button:hover {
  background-color: #c82333;
}

.Profile-ConatainerLeft .password-input {
  position: relative;
}

.Profile-ConatainerLeft .password-input input {
  padding-right: 2.5rem;
}

.Profile-ConatainerLeft .password-input svg {
  position: absolute;
  top: 50%;
  right: 0.5rem;
  transform: translateY(-50%);
  cursor: pointer;
}

.Profile-CPButton{
  margin: 10px;
  padding: 10px;
  width: 150px;
}
.change-password{
  display: flex;
  justify-content: center;
}

/* Holiday page css */
.Holiday-mainContainer {
  font-family: Arial, sans-serif;
  margin: 20px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: var(--colorWhite);
}

.Holiday-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.Holiday-header h1 {
  font-size: 24px;
  color: #333;
}

.Holiday-header button {
  padding: 10px 20px;
  font-size: 16px;
  color: var(--colorWhite);
  background-color: var(--colorMainLight);
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal input[type="text"],
.modal input[type="date"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.modal button {
  padding: 10px 20px;
  font-size: 16px;
  color: var(--colorWhite);
  background-color: var(--colorMainLight);
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.modal button:hover,
.Holiday-header button:hover {
  background-color: var(--colorMain);
}

.modal label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  color: #333;
}

.modal-content {
  width: 300px;
  padding: 20px;
  background-color: var(--colorWhite);
  border-radius: 5px;
}

.modal-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.modal-content h2 {
  font-size: 20px;
  color: #333;
}

.close-icon {
  font-size: 30px;
  cursor: pointer;
}
