.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: rgba(5, 55, 255, 0.075);
}

.login-header{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  margin: 0px 0px 20px 0px;
  color: var(--colorMain);
}

.login-header .login-logo,.login-title{
  display: flex;
  justify-content: center;
  align-items: center;
}
.login-header p{
  font-size: 24px;
  font-weight: bolder;
}
.login-logo img{
  width: 30px;
}

.email-display{
  display: flex;
  font-size: 14px;
  font-weight: 600;
  flex-wrap: wrap;
  margin-bottom: 10px;
}
.email-display p span{
  font-size: 16px;
  font-weight: bold;
}

.dark {
  background-color: var(--colorBlack);
  color: var(--colorWhite);
}

form {
  display: flex;
  flex-direction: column;
  width: 300px;
  background-color: rgba(255, 255, 255, 0.507);
  border-radius: 20px 20px 20px 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
  padding: 1.5rem;
  margin-top: 10px;
  border: 2px solid var(--colorWhite);
}

.input-group {
  position: relative;
  margin-bottom: 1.5rem;
}

.input-group input {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.input-group input:focus {
  outline: none;
  border-color: var(--colorMainLight);
  box-shadow: 0 0 0 2px #cce2ff;
}

.input-group input:focus + label {
  top: -10px;
  left: 10px;
  font-size: 12px;
  color: var(--colorMainLight);
}

.input-group label {
  position: absolute;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
  font-size: 16px;
  color: #999;
  pointer-events: none;
  transition: 0.2s ease-in-out;
}

.input-group-password input.invalid-password {
  border-color: red;
}

.input-group-password {
  position: relative;
}

.input-group-password input {
  padding-right: 40px; /* Adjust the padding to accommodate the button */
}

.input-group-password .button-eye {
  position: absolute;
  right: 10px;
  top: 25%;
  transform: translateY(-50%);
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  color: var(--colorMainLight);
}

.input-group-password .button-eye.visible {
  color: var(--colorMain); /* Change the color to indicate the password is visible */
}

.button-group {
  display: flex;
  justify-content: space-between;
}

button {
  padding: 0.5rem 1rem;
  font-size: 12px;
  margin-bottom: 1px;
  font-weight: bold;
  color: var(--colorWhite);
  background-color: var(--colorButtonLight);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}

button:hover {
  background-color: var(--colorButton);
}

.button-eye {
  padding: 5px;
  margin-top: 10px;
  color: var(--colorWhite);
  background-color: var(--colorMainLight);
  border: none;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}

.error-message {
  margin-top: 0.5rem;
  font-size: 14px;
  color: #ff4c4c;
}

.theme-toggle {
  margin-top: 1.5rem;
  background-color: rgba(255, 255, 255, 0.507);
  border-radius: 50%;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
  border: 2px solid var(--colorWhite);
}
